var _self;
export default {
	data() {
		return {
			num:1
		}
	},
	onLoad() {
		
	},
	onShow() {
		_self = this;
		if(_self.num == 1){
			_self.switch(_self.num);
		}
	},
	methods: {
		check(){
			_self.num = 0;
			uni.scanCode({
			    success: function (res) {
			        //console.log('条码类型：' + res.scanType);
			        //console.log('条码内容：' + res.result);
					
					if(res.result.indexOf("jfcm_sym") != -1){
						_self.num = 1;
						uni.navigateTo({
							url:"/pages/shop/canuse_ticket?id="+res.result.split("=")[1]
						})
					}else{
						_self.tipMsg("请扫描正确的收银码");
						_self.num = 1;
					}
					
			    }
			});
		},
		switch(){
			_self.num = 0;
			console.log('用户点击确定');
			uni.scanCode({
				success: function (res) {
					//console.log('条码类型：' + res.scanType);
					//console.log('条码内容：' + res.result);
					if(res.result.indexOf("jfcm_sym") != -1){
						uni.navigateTo({
							url:"/pages/shop/canuse_ticket?id="+res.result.split("=")[1]
						})
					}else{
						_self.tipMsg("请扫描正确的收银码");
						_self.num = 1;
					}
					
				}
			});
		}
	}
}
